.icons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.return {
  margin: 30px 38px;
  font-size: 20px;
  cursor: pointer;
}

.return:hover {
  color: #86e315;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px auto 30px;
  width: 300px;
}

.user-data {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
}